import { useTranslation } from "react-i18next";
import "./CardWithImg.css";

function CardWithImg({ cardData }) {
  const { t } = useTranslation();
  return (
    <div className="card-without-link">
      <div className="row">
        <div className="col-3 col-sm-12">
          <img src={cardData.imgSrc} alt={cardData.imgSrc} />
        </div>
        <div className="col-9 col-sm-12">
          <h5>{t(cardData.topTxt)}</h5>
          <p>{t(cardData.subTxt)}</p>
        </div>
      </div>
    </div>
  );
}

export default CardWithImg;
